import {
  HTTP_INTERCEPTORS,
  HttpErrorResponse,
  HttpEvent,
  HttpHandler,
  HttpInterceptor,
  HttpRequest,
  HttpResponse,
} from '@angular/common/http';
import { Injectable, Provider } from '@angular/core';
import { Observable, of } from 'rxjs';
import { delay, map, tap } from 'rxjs/operators';
import { unhandledError } from './error-responses';
import { actionItemsToResponse, generateId } from './helpers';

export const ACCOUNT_IDS_ERRORS: { [key: string]: HttpErrorResponse } = {
  // 2nd account from top
  '1cdb2224-8926-4b4d-a99f-1c9dfbbb4692': unhandledError,
};

@Injectable()
export class ActionsInterceptor implements HttpInterceptor {
  private readonly actionsGetErrorCode = parseInt(localStorage.getItem('actionsGetErrorCode') || '', 10);

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    if (localStorage.getItem('enableMocks') !== 'true') {
      return next.handle(req);
    }

    // empty response in mocks data module causes error
    // PUT for /action-recipes, excluding /action-recipes/batch
    if (req.url.match(/\/action-recipes\/(?!batch).*$/) && req.method === 'PUT') {
      const response = new HttpResponse({
        status: 202,
      });

      return of(response).pipe(
        delay(500),
        tap(() => console.log('CUSTOM INTERCEPTOR:', req, response)),
      );
    }

    if (req.url.match(/\/action-recipes\/batch$/) && req.method === 'PUT') {
      return next.handle(req).pipe(
        map((event: HttpEvent<any>) => {
          if (event instanceof HttpResponse) {
            if (!req.body || !req.body.actionRecipes) return event;

            const recipe = req.body.actionRecipes[0];

            if (ACCOUNT_IDS_ERRORS[recipe.arrangementId]) {
              const errorResponse = ACCOUNT_IDS_ERRORS[recipe.arrangementId];

              console.log('[ACTIONS INTERCEPTOR]', req, errorResponse);
              throw errorResponse;
            }

            const response = event.clone({
              body: {
                actionRecipes: [
                  {
                    id: generateId(),
                    ...recipe,
                    actions: actionItemsToResponse(recipe.actions),
                    recurrence: {
                      daysOfWeek: recipe?.recurrence?.daysOfWeek,
                      hoursOfDay: recipe?.recurrence?.hoursOfDay
                        ?.map((hour: string) => Number(hour))
                        .sort((a: number, b: number) => a - b),
                    },
                  },
                ],
              },
            });

            console.log('[ACTIONS INTERCEPTOR]', req, response);

            return response;
          }

          return event;
        }),
      );
    }

    if (req.url.match(/\/action-recipes$/) && req.method === 'GET') {
      return next.handle(req).pipe(
        map((event: HttpEvent<any>) => {
          if (this.actionsGetErrorCode) {
            const errorResponse = new HttpErrorResponse({
              status: this.actionsGetErrorCode,
            });

            console.log('[ACTIONS INTERCEPTOR]', req, errorResponse);
            throw errorResponse;
          }

          return event;
        }),
      );
    }

    return next.handle(req);
  }
}

export const ActionsMocksProvider: Provider = {
  provide: HTTP_INTERCEPTORS,
  useClass: ActionsInterceptor,
  multi: true,
};
