import { ActionParent, Action } from '@backbase/data-ang/actions';

export const generateId = () => Math.random().toString(16).slice(2);

export const actionItemsToResponse = (items: ActionParent[]): Action[] =>
  ['notification', 'email', 'sms'].map(type => ({
    type,
    enabled: items.findIndex(item => item.type === type) !== -1,
  }));
